export default {
    //日期转换
    timeFormat(time){
        var ymd = new Date(time).toLocaleString();
        var hm = new Date(time).toTimeString();
        var i = ymd.indexOf(' ');
        ymd = ymd.slice(0,i)+'\xa0\xa0\xa0';
        var reg=/\d{2}:\d{2}/g;
        return ymd + hm.match(reg);
      }
}