<template>
<div>
    <div v-show="!is_showpage">数据加载中。。。。。</div>
    <div v-show="is_showpage">
        <router-link to='/home'>查看账单</router-link>
        
        <br><br>

        <router-link to="/about">记账</router-link>
    </div>
</div>

</template>
<script>
export default {
    data(){
        return {
            is_showpage:false
        }
    },
    methods:{
        loadHome(){
            console.log('index')
        if(this.$store.getters.getGdata.bills){
            this.is_showpage=true;
        }else{
          requestAnimationFrame(()=>{
            this.loadHome();
          })
        }
      }
    },
    created(){
        this.loadHome()
    }
}
</script>