import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    g_data:[]
  },
  getters:{
    getGdata(state){
      return state.g_data;
    }
  },
  mutations: {
    setGdata(state,new_val){
      state.g_data = new_val;
      console.log('修改成功')
    }
  },
  actions: {
  },
  modules: {
  }
})
